/* global jQuery  */

/*
 * Swiper for customer quotes in hero
 */


(function ($) {

	"use strict";

	// video overlay hide on play

	$(".section9 .play-icon, .section9 .video-link").on("click", function(){
		$(".section9 .video-overlay").addClass("hidden");
	});

	// video playlist

	$(".section9 .video-link").on("click", function(){
		var ths = $(this);
		var videoText = ths.text();
		$(".section9 .active-video").text( videoText ).removeClass("menu-active");
		$(".section9 .video-links-list").fadeOut( "fast" );
	});

	$(".section9 .active-video").on("click", function(){
		$(".section9 .video-links-list").fadeToggle( "fast" );
		$(this).toggleClass("menu-active");
	});

	$(document).mouseup(function(e) {
		var container = $(".section9 .video-playlist");

		if (!container.is(e.target) && container.has(e.target).length === 0){
			$(".section9 .video-links-list").hide();
			$(".section9 .active-video").removeClass("menu-active");
		}
	});

}(jQuery));

